<template>
	<div>
		<p></p>
		<custom-form name="test"
					 @on-submit="onSubmit"
					 :items="formRule"
					 :data="data"
					 :rules="rules">
		</custom-form>
	</div>
</template>

<script>
    import CustomForm from '../../components/custom-form/index.vue';

    export default {
        data () {
            return {
                formRule: {
                    name: {
                        type: 'input',
                        label: '',
                        placeholder: '',
                    },
                    region: {
                        type: 'select',
                        label: '',
                        placeholder: '',
                        options: [{
                            label: '',
                            value: 'shanghai',
                        },{
                            label: '',
                            value: 'beijing'
                        },{
                            label: '',
                            value: ''
                        }]
                    },
                    delivery: {
                        type: 'switch',
                        label: ''
                    },
                    date: {
                        type: 'date',
                        label: '',
                    },
                    type: {
                        type: 'checkbox',
                        label: '',
                        options: [{
                            label: '/',
                            value: 1
                        }, {
                            label: '',
                            value: 2
                        }, {
                            label: '',
                            value: 3,
                            disabled: true
                        }, {
                            label: '',
                            value: 4
                        }]
                    },
                    resource: {
                        type: 'radio',
                        label: '',
                        options: [{
                            label: '',
                            value: '0'
                        }, {
                            label: '',
                            value: '1'
                        }]
                    }
                },
                data: {
                    name: '',
                    region: '',
                    date: '',
                    delivery: false,
                    type: [],
                    resource: '0',
                    desc: ''
                },
                rules: {
                    name: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
                    ],
                    region: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    date1: [
                        { type: 'date', required: true, message: '', trigger: 'change' }
                    ],
                    date2: [
                        { type: 'date', required: true, message: '', trigger: 'change' }
                    ],
                    type: [
                        { type: 'array', required: true, message: '', trigger: 'change' }
                    ],
                    resource: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    desc: [
                        { required: true, message: '', trigger: 'blur' }
                    ]
                }
            }
        },

        methods: {
            onSubmit(data) {
                console.log(data)
            }
        },

        components: {
            CustomForm
        }
    };
</script>

<style lang="css" scoped>
</style>



