<template>
    <div>
        <!---->
        <el-col :span="24" class="toolbar">
            <el-button type="primary" size="midium" @click="handleFilterAdd"></el-button>
            <el-button type="primary" size="midium" @click="handleAdd"></el-button>
        </el-col>
        <!--:rules="filterFormRules" -->
<!--        <el-dialog title="" :visible.sync="filterVisible" :close-on-click-modal="false">
            <custom-form name="filter"
                         @on-submit="onFilterSumbit"
                         :items="filterForm"
                         :data="filters"
                         >
            </custom-form>
        </el-dialog>-->

        <el-dialog title="" :visible.sync="filterVisible" :close-on-click-modal="false" @close="onFilterClose">
            <el-form :model="filters" label-width="150px" ref="addForm" :rules="filterRules">
                <el-form-item label="" prop="departmentId">
                    <el-select v-model="filters.departmentId" placeholder="" @change="selectFilterDepartment">
                        <el-option
                                v-for="item in allDepartments"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="requestorId">
                    <el-select v-model="filters.requestorId" placeholder="">
                        <el-option
                                v-for="item in filterRequestors"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-col :span="11">
                        <el-date-picker
                                :editable="false"
                                v-model="filters.requestTime_start"
                                value-format="yyyy-MM-dd HH:mm:ss"
                                type="datetime"
                                style="width: 100%;"
                                :picker-options="timeOptions">
                        </el-date-picker>
                    </el-col>
                    <el-col class="line" :span="2"></el-col>
                    <el-col :span="11">
                        <el-date-picker
                                :editable="false"
                                v-model="filters.requestTime_end"
                                value-format="yyyy-MM-dd HH:mm:ss"
                                type="datetime"
                                style="width: 100%;"
                                :picker-options="timeOptions">
                        </el-date-picker>
                    </el-col>
                </el-form-item>


                <el-form-item label="">
                    <el-col :span="11">
                        <el-input v-model.number="filters.price_start" placeholder=""></el-input>
                    </el-col>
                    <el-col class="line" :span="2"></el-col>
                    <el-col :span="11">
                        <el-input v-model.number="filters.price_end" placeholder=""></el-input>
                    </el-col>
                </el-form-item>

                <el-row>
                    <el-form-item align="right">
                        <el-button type="primary"  @click="refreshData()"></el-button>
                        <el-button type="primary"  @click="handleResetFilter()"></el-button>
                    </el-form-item>
                </el-row>
            </el-form>
        </el-dialog>

        <el-table :data="buyOrders" ref="buyOrders" v-loading="listLoading" :span-method="arraySpanMethod"
                  @selection-change="selsChange"  @sort-change="sortAll" style="width: 100%;">
            <el-table-column type="selection" width="55">
            </el-table-column>
            <el-table-column type="index" width="60" align="center">
            </el-table-column>
            <el-table-column label="" align="center" width="800">
                <el-table-column label="" prop="storeOperationItems_name" min-width="120"  align="center"></el-table-column>
                <el-table-column label="" prop="storeOperationItems_sort" min-width="120"  align="center"></el-table-column>
                <el-table-column label="" prop="storeOperationItems_spec" min-width="120"  align="center"></el-table-column>
                <el-table-column label="" prop="storeOperationItems_unit" min-width="120"  align="center"></el-table-column>
                <el-table-column label="" prop="storeOperationItems_number" min-width="120"  align="center"></el-table-column>
                <el-table-column label="" prop="storeOperationItems_price" min-width="120"  align="center"></el-table-column>
            </el-table-column>

            <el-table-column label=""  align="center" width="1060">
                <el-table-column label="" prop="approvalResult"
                                 :filters="[{text: '', value: '1'}, {text: '', value: 2}, {text: '', value: 0}]"
                                 :filter-method="filterTag"
                                 min-width="200"
                                 align="center">
                    <template slot-scope="scope">
                        <el-popover trigger="hover" placement="top" :disabled="showPopover(scope.row.approvalResult)">
                            <el-button type="primary" round size="small" @click="approve(scope.row.id, 1)"></el-button>
                            <el-button type="danger" round size="small" @click="approve(scope.row.id, 0)"></el-button>
                            <div slot="reference">
                                <el-tag :type="tagChange(scope)">{{scope.row.tag}}</el-tag>
                            </div>
                        </el-popover>
                    </template>
                </el-table-column>

                <el-table-column label="" prop="type" min-width="200" :formatter="formatTypeFlag" sortable="custom" align="center"></el-table-column>
                <el-table-column label="" prop="departmentName" min-width="140" sortable="custom" align="center"></el-table-column>
                <el-table-column label="" prop="requestorName" min-width="140" sortable="custom" align="center"></el-table-column>
                <el-table-column label="" prop="requestTime" min-width="200" sortable="custom" align="center"></el-table-column>
                <el-table-column label="" prop="requestTotalPrice" min-width="140" sortable="custom" align="center"></el-table-column>
                <el-table-column label="" prop="description" min-width="200" sortable="custom" align="center"></el-table-column>
                <el-table-column label="" prop="opinion" min-width="200" sortable="custom" align="center"></el-table-column>
            </el-table-column>

            <el-table-column label="" width="320" align="center">
                <el-table-column prop="creatorName" label="" width="120" sortable="custom" align="center">
                </el-table-column>
                <el-table-column type="date" prop="createTime" label="" min-width="180" sortable="custom" align="center">
                </el-table-column>
                <el-table-column prop="updatorName" label="" min-width="180" sortable="custom" align="center">
                </el-table-column>
                <el-table-column type="date" prop="lastmodifiedTime" label="" min-width="180" sortable="custom" align="center">
                </el-table-column>
            </el-table-column>

            <el-table-column label="" width="150" fixed="right">
                <template slot-scope="scope">
                    <el-button size="small" @click="handleEdit(scope.$index, scope.row)" :disabled="checkInApproval(scope.row.approvalResult)"></el-button>
                    <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!--   -->
        <el-col :span="24" class="toolbar">
            <el-button type="danger" :disabled="this.sels.length === 0" @click="batchRemove"></el-button>
            <el-popover
                    trigger="click"
                    ref="batchApprovalPopover"
                    placement="top-start">
                    <el-button size="mini" type="primary" @click="batchApprove(1)"></el-button>
                    <el-button type="danger" size="mini" style="float: right;" @click="batchApprove(0)"></el-button>
            </el-popover>
            <el-button type="primary" :disabled="this.sels.length === 0" v-popover:batchApprovalPopover></el-button>
            <el-pagination
                    class="pagination"
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pagination.currentPage"
                    :page-sizes="pagination.pageSizes"
                    :page-size="pagination.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pagination.total">
            </el-pagination>
        </el-col>


        <!---->
        <el-dialog fullscreen title="" :visible.sync="editFormVisible" :close-on-click-modal="false" @close="clearValidate('editForm')">
            <el-form :model="editObj" label-width="80px" :rules="editFormRules" ref="editForm">
                <el-form-item label="" prop="departmentId">
                    <el-select v-model="editObj.departmentId" placeholder="" @change="selectEditDepartment">
                        <el-option
                                v-for="item in allDepartments"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="requestorId">
                    <el-select v-model="editObj.requestorId" placeholder="" >
                        <el-option
                                v-for="item in editRequestors"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="requestTime">
                    <el-date-picker
                            :editable="false"
                            v-model="editObj.requestTime"
                            value-format="yyyy-MM-dd HH:mm:ss"
                            type="datetime"
                            align="right"
                            :picker-options="timeOptions">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="" prop="type">
                    <el-select v-model="editObj.type" placeholder="" @change="selectEditDepartment">
                        <el-option
                                v-for="item in allTypes"
                                :key="item.value"
                                :label="item.name"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="storeOperationItems">

                    <el-table :data="editObj.storeOperationItems"
                              show-summary
                              :summary-method="getSummaries"
                              border
                              height="350px" max-height="999999px"
                              highlight-current-row
                              v-loading="listLoading"
                              @selection-change="selsChange"
                              style="width: 100%;">

                        <el-table-column label="" prop="name" align="center">
                            <template slot-scope="scope">
                                <el-autocomplete
                                        v-model="scope.row.name"
                                        :fetch-suggestions="querySearchAsync"
                                        placeholder=""
                                        @select="handleSelect(scope.row, $event)"
                                ></el-autocomplete>
                            </template>
                        </el-table-column>
                        <el-table-column label="" prop="number" align="center">
                            <template slot-scope="scope">
                                <el-input v-model="scope.row.number" placeholder=""></el-input>
                            </template>
                        </el-table-column>
                        <el-table-column label="" prop="spec" align="center">
                            <template slot-scope="scope">
                                <el-input v-model="scope.row.spec" placeholder=""></el-input>
                            </template>
                        </el-table-column>
                        <el-table-column label="" prop="price" align="center">
                            <template slot-scope="scope">
                                <el-input v-model="scope.row.price" placeholder=""></el-input>
                            </template>
                        </el-table-column>
                        <el-table-column label="" prop="unit" align="center">
                            <template slot-scope="scope">
                                <el-input v-model="scope.row.unit" placeholder=""></el-input>
                            </template>
                        </el-table-column>

                        <el-table-column
                                fixed="right"
                                label=""
                                width="50"  align="center">
                            <template slot-scope="scope">
                                <el-button type="danger" size="small" icon="el-icon-minus" @click="handleRemoveBuyOrderItem(scope.$index, editObj.storeOperationItems)">
                                </el-button>
                            </template>
                        </el-table-column>
                    </el-table>

                    <el-form-item style="margin-top: 10px;">
                        <el-button type="primary" size="medium" icon="el-icon-plus" @click="handleAddBuyOrderItem(editObj.storeOperationItems)"></el-button>
                    </el-form-item>

                </el-form-item>

                <el-form-item label="" prop="description">
                    <el-input type="textarea" :rows="4" v-model="editObj.description" autosize></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" :loading="editLoading" @click="handleEditFormSubmit('editForm')"></el-button>
            </div>
        </el-dialog>


        <!---->
        <el-dialog title="" fullscreen :visible.sync="addFormVisible" :close-on-click-modal="false" @close="clearValidate('addForm')">
            <el-form :model="addObj" label-width="80px" :rules="addFormRules" ref="addForm">
                <el-form-item label="" prop="departmentId">
                    <el-select v-model="addObj.departmentId" placeholder="" @change="selectAddDepartment">
                        <el-option
                                v-for="item in allDepartments"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="requestorId">
                    <el-select v-model="addObj.requestorId" placeholder="">
                        <el-option
                                v-for="item in addRequestors"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="requestTime">
                    <el-date-picker
                            :editable="false"
                            v-model="addObj.requestTime"
                            value-format="yyyy-MM-dd HH:mm:ss"
                            type="datetime"
                            align="right"
                            :picker-options="timeOptions">
                    </el-date-picker>
                </el-form-item>

                <el-form-item label="" prop="type">
                    <el-select v-model="addObj.type" placeholder="" @change="selectEditDepartment">
                        <el-option
                                v-for="item in allTypes"
                                :key="item.value"
                                :label="item.name"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="" prop="storeOperationItems">

                    <el-table :data="addObj.storeOperationItems"
                              show-summary
                              :summary-method="getSummaries"
                              border
                              height="350px" max-height="999999px"
                              highlight-current-row
                              v-loading="listLoading"
                              @selection-change="selsChange"
                              style="width: 100%;">

                        <el-table-column label="" prop="name" align="center">
                            <template slot-scope="scope">
                                <el-autocomplete
                                        v-model="scope.row.name"
                                        :fetch-suggestions="querySearchAsync"
                                        placeholder=""
                                        @select="handleSelect(scope.row, $event)"
                                ></el-autocomplete>
                            </template>
                        </el-table-column>
                        <el-table-column label="" prop="number" align="center">
                            <template slot-scope="scope">
                                <el-input v-model="scope.row.number" placeholder=""></el-input>
                            </template>
                        </el-table-column>
                        <el-table-column label="" prop="spec" align="center">
                            <template slot-scope="scope">
                                <el-input v-model="scope.row.spec" placeholder=""></el-input>
                            </template>
                        </el-table-column>
                        <el-table-column label="" prop="price" align="center">
                            <template slot-scope="scope">
                                <el-input v-model="scope.row.price" placeholder=""></el-input>
                            </template>
                        </el-table-column>
                        <el-table-column label="" prop="unit" align="center">
                            <template slot-scope="scope">
                                <el-input v-model="scope.row.unit" placeholder=""></el-input>
                            </template>
                        </el-table-column>

                        <el-table-column
                                fixed="right"
                                label=""
                                width="50"  align="center">
                                <template slot-scope="scope">
                                    <el-button type="danger" size="small" icon="el-icon-minus" @click="handleRemoveBuyOrderItem(scope.$index, addObj.storeOperationItems)">
                                    </el-button>
                            </template>
                        </el-table-column>
                    </el-table>

                    <el-form-item style="margin-top: 10px;">
                        <el-button type="primary" size="medium" icon="el-icon-plus" @click="handleAddBuyOrderItem(addObj.storeOperationItems)"></el-button>
                    </el-form-item>

                </el-form-item>

                <el-form-item label="" prop="description">
                    <el-input type="textarea" :rows="4" v-model="addObj.description" autosize></el-input>
                </el-form-item>
            </el-form>

            <div slot="footer" class="dialog-footer">
                <el-button type="primary" :loading="addLoading" @click="handleAddFormSubmit('addForm')"></el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import util from '../../common/js/util'
    import { requestApi, Enum, msgUtils } from '../../api/api';
    import { getNewDataAndMap } from '../../api/tableRowAndColumn';
    import CustomForm from '../../components/custom-form/index.vue';


    export default {
        data() {

            // 
            var checkUnitPrice = (rule, value, callback) => {
                if(!value) {
                    return callback(new Error(''));
                }
                let  rex = /^[0-9]+(.[0-9]{1,2})?$/;
                setTimeout(() => {
                    if(!rex.test(value)) {
                        callback("");
                    }else {
                        callback();
                    }
                }, 1000);
            };

            // 
            var checkBuyOrderItems = (rule, value, callback) => {
                var buyOrderItems = this.editObj.storeOperationItems;
                let  rexPrice = /^[0-9]+(.[0-9]{1,2})?$/;
                let  rexNum = /^[1-9]\d*$/;
                let bool = true;
                buyOrderItems.forEach(item => {
                    for(var i in item) {
                        // 
                        if(i === 'price') {
                            if(!rexPrice.test(item[i])) {
                                this.err_msg ="";
                                bool = false;
                            }
                        }
                        // 
                        if(i === 'number') {
                            if(!rexNum.test(item[i])) {
                                this.err_msg ='';
                                bool = false;
                            }
                        }

                        if(item[i] === '') {
                            this.err_msg = '';
                            bool = false;
                        }
                    }
                });
                if(bool) {
                    this.err_msg = '';
                }
                callback();
            };

            // 


            return {
                allTypes:[
                    {
                        name: '',
                        value: '1'
                    },
                    {
                        name: '',
                        value: '2'
                    }
                ],
                state2: '',
                visible2: false,
                // 
                listLoading: false,
                // id
                allDepartments: [],
                // userid
                allUsers: [],
                // 
                buyOrders: [],
                dataMap: [],
                rowIndex1: 0,
                // 
                approvalLoading: false,
                // 
                sels: [],
                // 
                batchApprovalLoading: false,
                // 
                pagination: {
                    total: 0,
                    currentPage: 1,
                    pageSizes: [10, 20, 50, 100],
                    pageSize: 10
                },
                selectRowLoading: false, //
                allLikeNames: [] , //input
                // 
                editObj: {},
                // departmentididname
                editRequestors: [],
                editFormVisible: false,//
                editLoading: false,  // 
                timeOptions: {
                    shortcuts: [{
                        text: '',
                        onClick(picker) {
                            picker.$emit('pick', new Date());
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24);
                            picker.$emit('pick', date);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', date);
                        }
                    }]
                },
                allSorts: [], // 
                nameBool: false,
                err_msg: '',
                editFormRules: {
                    departmentId: [
                        {required: true, message: '', trigger: 'change'}
                    ],
                    requestorId: [
                        {required: true, message: '', trigger: 'change'}
                    ],
                    createAdmin: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    createTime: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    updateAdmin: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    updateTime: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    requestTime: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    buyOrderItems: [
                        {required: true, message: '', trigger: 'blur'},
                        { validator: checkBuyOrderItems, trigger: 'blur' },
                    ],
                    price: [
                        { validator: checkUnitPrice, trigger: 'blur' },
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    sort: [
                        {required: true, message: '', trigger: 'change' }
                    ],
                    spec: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    unit: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                },
                errMsg: false,
                errMsg1: false,
                // filter
                filterVisible: false,
                filterRequestors: [],
                filters: {
                    price_start: '',
                    price_end: '',
                    requestorId: '',
                    departmentId: '',
                    requestTime_start: '',
                    requestTime_end: ''
                },
                filterRules: {
                    price_start: [
                        { type:'number', min: 0,  message: '0', trigger: 'blur' }
                    ],
                    price_end: [
                        { type:'number', min: 0,  message: '0', trigger: 'blur' }
                    ],
                    requestTime_start: [
                        { type: 'date', message: '', trigger: 'change' }
                    ],
                    requestTime_end: [
                        { type: 'date', message: '', trigger: 'change' }
                    ]
                },

                // 
                addFormVisible: false,
                addLoading: false,
                canAdd: true,
                addObj: {
                    departmentId: '',
                    requestTime: '',
                    requestorId: '',
                    description: '',
                    storeOperationItems: []
                },
                // departmentididname
                addRequestors: [],
                addFormRules: {
                    departmentId: [
                        {required: true, message: '', trigger: 'change'}
                    ],
                    requestorId: [
                        {required: true, message: '', trigger: 'change'}
                    ],
                    requestTime: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                }
            };
        },
        components: {
            CustomForm
        },
        methods: {
            //   
            formatTypeFlag(row, column) {
                if(row.type === 1){
                    return '';
                }
                else if(row.type === 2){
                    return '';
                }
                else{
                    return ''
                }
            },
            // 
            sortAll(sort) {
                var propName = sort.prop;
                if(propName === 'creatorName'){
                    propName = 'createBy';
                }
                if(propName === 'updatorName'){
                    propName = 'lastmodifiedBy';
                }
                // nameId
                propName = propName.replace('Name','Id');
                this.filters.property = propName;
                if(sort.order === 'ascending'){
                    this.filters.direction = 'ASC';
                }
                if(sort.order === 'descending'){
                    this.filters.direction = 'DESC';
                }
                this.getBuyOrders();
            },

            // filter
            onFilterClose() {
                this.getBuyOrders();
            },
            refreshData() {
                this.getBuyOrders();
                this.filterVisible = false;
            },
            handleResetFilter() {
                this.filters = {
                    price_start: '',
                    price_end: '',
                    requestorId: '',
                    departmentId: '',
                    requestTime_start: '',
                    requestTime_end: ''
                }
            },
            handleFilterAdd() {
                this.filterVisible = true;
            },
            selectFilterDepartment(val) {
                // formData
                let formData  = new FormData();
                formData .append('departmentId', val);
                requestApi.user.findByDepartment(this, formData).then(res => {
                    res = res.body;
                    if(res.code === Enum.SUCCESS.code) {
                        this.filterRequestors = res.data;
                    }else {
                        msgUtils.warning(this, res.msg);
                    }
                }, error=>{
                    msgUtils.error(this, Enum.SYSTEM_ERROR.msg);
                });
            },
            checkAddBuyOrderItems() {
                var buyOrderItems = this.addObj.storeOperationItems;
                let  rexPrice = /^[0-9]+(.[0-9]{1,2})?$/;
                //let  rexNum = /^[1-9]\d*$/;
                let  rexNum =  /^\d+$/;
                this.canAdd = true;
                if(buyOrderItems.length === 0){
                    // 
                    this.canAdd = false;
                    msgUtils.warning(this, '');
                    return;
                }
                buyOrderItems.forEach(item => {
                    // 
                    if(item.name === '' ){
                        msgUtils.warning(this, '');
                        this.canAdd = false;
                        return;
                    }
                    // 
                    if(!rexPrice.test(item.price)) {
                        msgUtils.warning(this, '');
                        this.canAdd = false;
                        return;
                    }
                    // 
                    if(!rexNum.test(item.number)) {
                        msgUtils.warning(this, '');
                        this.canAdd = false;
                        return;
                    }
                    for(var i in item){
                        if(item[i] === '') {
                            msgUtils.warning(this, '');
                            this.canAdd = false;
                            return;
                        }
                    }
                });
            },

            getSummaries(param) {
                const { columns, data } = param;
                const sums = [];
                columns.forEach((column, index) => {
                    if (index === 0) {
                        sums[index] = '';
                        return;
                    }else if(column.property === 'number'){
                        let total_number = 0;
                        data.forEach(item => {
                            total_number = total_number + Number(item[column.property]);
                        });
                        sums[index] = ': ' + total_number;
                    }else if(column.property === 'price'){
                        let total_price = 0;
                        data.forEach(item => {
                            total_price = total_price + Number(item['number'])*Number(item[column.property]);
                        });
                        sums[index] = ': ' + total_price.toFixed(2) + ' ';
                    }else{
                        sums[index] = '';
                    }
                });
                return sums;
            },

            handleSelect(buyOrderItem, selected_item) {
                buyOrderItem.goodId = selected_item.id;
                buyOrderItem.name = selected_item.name;
                buyOrderItem.sort = selected_item.sort;
                buyOrderItem.spec = selected_item.spec;
                buyOrderItem.unit = selected_item.unit;
                buyOrderItem.price = selected_item.price;
            },
            // 
            querySearchAsync(queryString, cb) {
                if (queryString !== '') {
                    this.loading = true;
                    // goodItem
                    requestApi.good.findByName(this, queryString).then(res => {
                        res = res.body;
                        if(res.code === Enum.SUCCESS.code){
                            //this.allLikeNames = res.data;
                            // value
                            let goods = res.data;
                            goods.forEach(good => {
                                good.value = good.name + "(" + good.spec + ")";
                            });
                            cb(goods);
                        }else{
                            msgUtils.warning(this, res.msg);
                        }
                    },err => {
                        msgUtils.error(this, Enum.SYSTEM_ERROR.msg);
                    });
                }
            },
            handleCurrentChange(val) {
                this.pagination.currentPage = val;
                this.getBuyOrders();
            },
            handleSizeChange(val) {
                this.pagination.pageSize = val;
                this.getBuyOrders();
            },
            getBuyOrders() {
                this.listLoading = true;
                // 
                requestApi.department.findAll(this, 0, 1000).then(res => {
                    res = res.body;
                    if(res.code === Enum.SUCCESS.code){
                        this.allDepartments = res.data.content;
                    }else{
                        msgUtils.warning(this, res.msg);
                    }
                },err => {
                    msgUtils.error(this, Enum.SYSTEM_ERROR.msg);
                });
                // 
                requestApi.good.findAllSorts(this).then(res => {
                    res = res.body;
                    if(res.code === Enum.SUCCESS.code){
                        this.allSorts = res.data;
                    }else{
                        msgUtils.warning(this, Enum.GET_SORTS_FAIL.msg);
                    }
                },err => {
                    msgUtils.error(this, Enum.SYSTEM_ERROR.msg);
                });
                // 
                let formData  = new FormData();
                formData .append('page', this.pagination.currentPage);
                formData .append('size', this.pagination.pageSize);
                formData .append('price_start', this.filters.price_start);
                formData .append('price_end', this.filters.price_end);
                formData .append('departmentId', this.filters.departmentId);
                formData .append('requestorId', this.filters.requestorId);
                formData .append('requestTime_start', this.filters.requestTime_start);
                formData .append('requestTime_end', this.filters.requestTime_end);
                if(this.filters.property && this.filters.direction){
                    formData .append('property', this.filters.property);
                    formData .append('direction', this.filters.direction);
                }
                requestApi.storeOperation.findByParam(this, formData).then(res => {
                    this.listLoading = false;
                    res = res.body;
                    if(res.code === Enum.SUCCESS.code){
                        this.pagination.total = res.data.total;
                        let rs = getNewDataAndMap("storeOperationItems",res.data.data);
                        this.buyOrders = rs["data"];
                        this.dataMap = rs["map"];
                    }
                }, err => {
                    msgUtils.error(this, Enum.SYSTEM_ERROR.msg);
                });
            },

            // 
            clearValidate(formName) {
                this.$refs[formName].clearValidate();
                this.canAdd = false;
                // 
                this.addRequestors  = [];
            },

            arraySpanMethod({ row, column, rowIndex, columnIndex }) {
                let map = this.dataMap;
                if (columnIndex < 2 || columnIndex > 7) {
                    if(map[rowIndex] != null){
                        return {
                            rowspan: map[rowIndex],
                            colspan: 1
                        };
                    }else{
                        return {
                            rowspan: 0,
                            colspan: 0
                        };
                    }
                }
            },

            // 
            handleAdd() {
                this.addFormVisible = true;
                // addObj
                util.emptyObj(this.addObj);
            },

            // 
            selectAddDepartment(value) {
                this.addRequestors = [];
                // formData
                let formData  = new FormData();
                formData .append('departmentId', value);
                requestApi.user.findByDepartment(this, formData).then(res => {
                    res = res.body;
                    if(res.code === Enum.SUCCESS.code) {
                        this.addRequestors = res.data;
                    }else {
                        msgUtils.warning(this, res.msg);
                    }
                }, error=>{
                    msgUtils.error(this, Enum.SYSTEM_ERROR.msg);
                });
            },

            // 
            handleRemoveBuyOrderItem(index, buyOrderItems) {
                buyOrderItems.splice(index, 1);
            },

            // 
            handleAddBuyOrderItem(buyOrderItems) {
                let obj = {
                    name: '',
                    sort: '',
                    spec: '',
                    unit: '',
                    number: '',
                    price: ''
                };
                buyOrderItems.push(obj);
            },

            // 
            handleAddFormSubmit(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.checkAddBuyOrderItems();
                        if(this.canAdd) {
                            this.$confirm('', '', {}).then(() => {
                                this.addLoading = true;
                                let para = this.addObj;
                                requestApi.storeOperation.add(this, para).then(res => {
                                    res = res.body;
                                    this.addFormVisible = false;
                                    this.addLoading = false;
                                    if(res.code === Enum.SUCCESS.code){
                                        this.getBuyOrders();
                                    }else{
                                        msgUtils.warning(res.msg);
                                    }
                                }, err => {
                                    this.addFormVisible = false;
                                    this.addLoading = false;
                                    msgUtils.error(Enum.SYSTEM_ERROR.msg);
                                });
                            });
                        }else {
                            return false;
                        }
                    } else {
                        return false;
                    }
                });
            },

            // 
            quickInput() {
                let para = [];
                let bool = true;
                this.sels.forEach(item => {
                    if(item.approvalResult !== 1) {
                        bool = false;
                    }else {
                        para.push(item.id);
                    }
                });
                if(!bool) {
                    msgUtils.warning(this, '');
                    return false;
                }
                this.$confirm('', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    requestApi.storeOperation.quickInput(this, para).then(res => {
                        this.listLoading = false;
                        res = res.body;
                        if(res.code === Enum.SUCCESS.code){
                            msgUtils.success(this, "");
                            this.getBuyOrders();
                        }else{
                            msgUtils.warning(res.msg);
                        }
                    },error => {
                        this.listLoading = false;
                        msgUtils.error(this, Enum.SYSTEM_ERROR.msg);
                    });
                });
            },

            // popover
            showPopover(approvalResult) {
                let bool = false;
                if(approvalResult === 2) {
                    bool = false;
                }else {
                    bool = true;
                }
                return bool;
            },

            // 
            approve(id, approvalResult) {
                let obj = {
                    id: id,
                    approvalResult: approvalResult
                };
                let para = [];
                para.push(obj);
                this.$confirm('', '', {}).then(() => {
                    this.listLoading = true;
                    requestApi.storeOperation.approve(this, para).then(res => {
                        this.listLoading = false;
                        res = res.body;
                        if (res.code === Enum.SUCCESS.code) {
                            this.getBuyOrders();
                        } else {
                            msgUtils.warning(this, res.msg);
                        }
                    }, error => {
                        this.listLoading = false;
                        msgUtils.error(this, Enum.SYSTEM_ERROR.msg);
                    });
                });
            },

            // (approveResult2)
            checkInApproval(approvalResult) {
                let bool = false;
                if(approvalResult === 2) {
                    bool = false;
                }else {
                    bool = true;
                }
                return bool;
            },

            // 
            selsChange: function (sels) {
                this.sels = sels;
            },

            // 
            batchApprove(approvalResult){
                let approvalArr = [];
                let bool = true;
                this.sels.forEach(item => {
                    if(item.approvalResult === 2) {
                        let obj = {
                            id: item.id,
                            approvalResult: approvalResult
                        };
                        approvalArr.push(obj);
                    }else {
                        bool = false;
                    }
                });
                if(bool === false) {
                    msgUtils.warning(this, '');
                    return false;
                }
                let model_msg = '';
                if(approvalResult === 1) {
                    model_msg = '';
                }else {
                    model_msg = '';
                }
                this.$confirm(model_msg, '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    requestApi.storeOperation.approve(this, approvalArr).then(res => {
                        this.listLoading = false;
                        res = res.body;
                        if(res.code === Enum.SUCCESS.code) {
                            this.getBuyOrders();
                        }else {
                            msgUtils.warning(this, res.msg);
                        }
                    }, err => {
                        this.listLoading = false;
                        msgUtils.error(this, Enum.SYSTEM_ERROR.msg);
                    });
                });
            },

            // 
            batchRemove() {
                let para = this.sels.map(item => {
                    return {id: item.id};
                });
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    requestApi.storeOperation.deletes(this, para).then(res => {
                        this.listLoading = false;
                        res = res.body;
                        if(res.code === Enum.SUCCESS.code) {
                            this.getBuyOrders();
                        }else {
                            msgUtils.warning(this, res.msg);
                        }
                    }, err => {
                        this.listLoading = false;
                        msgUtils.error(this, Enum.SYSTEM_ERROR.msg);
                    });
                });
            },

            // 
            handleEdit(index,row) {
                this.listLoading = true;
                this.editRequestors = [];
                // formData
                let formData_0  = new FormData();
                formData_0 .append('departmentId', row.departmentId);
                requestApi.user.findByDepartment(this, formData_0).then(res => {
                    res = res.body;
                    if(res.code === Enum.SUCCESS.code) {
                        this.editRequestors = res.data;
                    }else {
                        msgUtils.warning(this, res.msg);
                    }
                }, error=>{
                    msgUtils.error(this, Enum.SYSTEM_ERROR.msg);
                });

                let formData = new FormData();
                formData.append('buyOrderId', row.id);
                requestApi.storeOperation.findById(this, formData).then(res => {
                    this.listLoading = false;
                    res = res.body;
                    this.editObj = res;
                }, error=>{
                    this.listLoading = false;
                    msgUtils.error(this, Enum.SYSTEM_ERROR.msg);
                });
                this.listLoading = false;
                this.editFormVisible = true;
            },

            // 
            selectEditDepartment(value) {
                this.editRequestors = [];
                // formData
                let formData  = new FormData();
                formData .append('departmentId', value);
                requestApi.user.findByDepartment(this, formData).then(res => {
                    res = res.body;
                    if(res.code === Enum.SUCCESS.code) {
                        this.addRequestors = res.data;
                    }else {
                        msgUtils.warning(this, res.msg);
                    }
                }, error=>{
                    msgUtils.error(this, Enum.SYSTEM_ERROR.msg);
                });
            },


            // 
            handleEditFormSubmit(formName) {

                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.checkAddBuyOrderItems();
                        if(this.canAdd) {
                            this.$confirm('', '', {}).then(() => {
                                this.editLoading = true;
                                let para = this.editObj;
                                requestApi.storeOperation.update(this, para).then(res => {
                                    res = res.body;
                                    this.editFormVisible = false;
                                    this.editLoading = false;
                                    if(res.code === Enum.SUCCESS.code){
                                        this.getBuyOrders();
                                    }else{
                                        msgUtils.warning(res.msg);
                                    }
                                }, err => {
                                    this.editFormVisible = false;
                                    this.editLoading = false;
                                    msgUtils.error(Enum.SYSTEM_ERROR.msg);
                                });
                            });
                        }else {
                            return false;
                        }
                    } else {
                        return false;
                    }
                });
            },

            // 
            handleDel(index, row) {
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    let para = { id: row.id };
                    requestApi.storeOperation.delete(this, para).then(res => {
                            this.listLoading = false;
                            res = res.body;
                            if(res.code === Enum.SUCCESS.code) {
                                this.getBuyOrders();
                            }else {
                                msgUtils.warning(this, res.msg);
                            }
                        }, err => {
                            this.listLoading = false;
                            msgUtils.error(this, Enum.SYSTEM_ERROR.msg);
                        });
                });
            },

            // 
            filterTag(value, row) {
                row.approvalResult === value;
            },

            // class
            tagChange(scope) {
                let approvalResult = scope.row.approvalResult;
                switch(approvalResult) {
                    case 0: {scope.row.tag = '';return 'danger';break;}
                    case 1: {scope.row.tag = '';return 'success';break;}
                    case 2: {scope.row.tag = '';return 'primary';break;}
                    default: {scope.row.tag = '';return 'primary';break;}
                }
            }
        },

        mounted() {
            this.getBuyOrders();
        }
    }

</script>

<style>
    .pagination {
        float: right;
    }
    .line {
        text-align: center;
    }
    .el-table__body-wrapper, .el-table__body-wrapper.is-scroll-right, .el-table__body-wrapper.is-scroll-left {
        overflow-y: hidden;
    }
</style>


